<template>
  <div>
  <div style="height:5px"></div>
  <el-drawer title="我是标题" :visible.sync="drawer" :with-header="false" size="210px">
         <time-count-down :totalTime="getTimes" @hand-in="headIn"></time-count-down>
        <div style="margin-top: 15px;margin-left:5px">
       
        <div id="ch" v-show="choiceEn">
        <span>选择题</span><br/>
        <el-row :gutter="20" style="margin-right:12px;margin-top:5px">
          <el-col :span="6" v-for="(item,index) in choice" :key="item.id"><div class="col" style="margin-top: 5px">
          <el-button class="law" :type="choiceAnswer[index] != null ? 'success' :''" @click="choiceSerial = index;mode=0;radio=choiceAnswer[index]">{{index+1}}</el-button>
          </div></el-col>

        </el-row>
  
        </div>
        
        <div id="mu" v-show="multipleEn" style="margin-top: 15px">
        <span>多选题</span><br/>
        <el-row :gutter="20" style="margin-right:12px;margin-top:5px">
          <el-col :span="6" v-for="(item,index) in multiple" :key="item.id"><div class="col" style="margin-top: 5px">
          <el-button class="law" :type="multipleAnswer[index] != null ? 'success' :''" @click="multipleSerial = index;mode=1;">{{index+1}}</el-button>
          </div></el-col>

        </el-row>
  
        </div>
        
        <div id="ju" style="margin-top: 15px" v-show="judgeEn">
        <span>判断题</span><br/>
        <el-row :gutter="20" style="margin-right:12px;margin-top:5px">
          <el-col :span="6" v-for="(item,index) in judge" :key="item.id"><div class="col" style="margin-top: 5px">
          <el-button class="law" :type="judgeAnswer[index] != null ? 'success' :''" @click="judgeSerial = index;mode=2;judgeRadio=judgeAnswer[index]">{{index+1}}</el-button>
          </div></el-col>

        </el-row>
  
        </div>
        <div style="margin-top:20px;margin-right:5px">
        <el-button v-show="showHind" type="success" id="hand" @click="headIn" style="width:100%;height:30px;">交卷</el-button>
        <br/>
        <el-button v-show="!showHind" type="info" id="handinfo" style="width:100%;height:30px;" @click="showtotal()">查看分数</el-button>
        </div>
        </div>

      </el-drawer>
    <div id="head">
    <div v-show="showTotal" style="margin-left:10px;margin-right:10px;" >
    <el-alert 
    :title="'您的成绩为：'+total"
    type="success"
    effect="dark">
  </el-alert>
  </div>
      <el-button type="text" icon="el-icon-arrow-left" style="margin-left: 10px" @click="toList()"
        >返回考试列表</el-button
      >
      
      <el-button type="text" icon="el-icon-tickets"   @click="drawer = true" style="float: right;margin-right:15px"
        >答题卡</el-button
      >
      <span v-show="choiceShow" style="text-align: center;display:block;">单选题</span>
      <span v-show="multipleShow" style="text-align: center;display:block;">多选题</span>
      <span v-show="judgeShow" style="text-align: center;display:block;">判断题</span>
    </div>

    <div id="center" style="margin-top: 5px;">
    <!-- height:450px -->
    <el-card class="box-card" v-show="choiceShow">
        <div>
          
          <span style="font-size: 20px">{{choiceSerial+1}}、</span>
          <div style="margin-top: 5px;margin-bottom: 15px">
            &nbsp;&nbsp;&nbsp;&nbsp;{{choice[choiceSerial].belong_question.question}}
          </div>
        </div>
        <div id="choice" style="margin-top:10px;">
        
          <el-radio-group v-model="radio" @change="radioChange" >
          <!-- <div v-for="item in order()" :key="item.id"> -->
           <el-radio :label="0" >A、{{choice[choiceSerial].belong_question.answer_A}}</el-radio><br/>
            <el-radio :label="1">B、{{choice[choiceSerial].belong_question.answer_B}}</el-radio><br/>
            <el-radio :label="2">C、{{choice[choiceSerial].belong_question.answer_C}}</el-radio><br/>
            <el-radio :label="3">D、{{choice[choiceSerial].belong_question.answer_D}}</el-radio><br/>
          <!-- </div> -->
           
          </el-radio-group>
          <div v-show="show" style="margin-top: 15px;margin-left:2px">
          <span>正确答案：</span><span style="color:#F56C6C">{{choice[choiceSerial].belong_question.right_answer}}</span>
          </div>
        </div>
      </el-card>
    <el-card class="box-card" v-show="multipleShow">
        <div>
          
          <span style="font-size: 20px">{{multipleSerial+1}}、</span>
          <div style="margin-top: 5px">
            &nbsp;&nbsp;&nbsp;&nbsp;{{multiple[multipleSerial].belong_question.question}}
          </div>
        </div>
        <div id="multiple" style="margin-top:10px;">
        <el-checkbox-group v-model="checkList" @change="checkboxChange">
          <el-checkbox label="A">A、{{multiple[multipleSerial].belong_question.answer_A}}</el-checkbox><br/>
          <el-checkbox label="B">B、{{multiple[multipleSerial].belong_question.answer_B}}</el-checkbox><br/>
          <el-checkbox label="C">C、{{multiple[multipleSerial].belong_question.answer_C}}</el-checkbox><br/>
          <el-checkbox label="D">D、{{multiple[multipleSerial].belong_question.answer_D}}</el-checkbox><br/>
        </el-checkbox-group>
         <div v-show="show" style="margin-top: 15px;margin-left:2px">
          <span>正确答案：</span><span style="color:#F56C6C">{{multiple[multipleSerial].belong_question.right_answer}}</span>
          </div>
        </div>
      </el-card>

      <el-card class="box-card" v-if="judge != null" v-show="judgeShow">
        <div>
          
          <span style="font-size: 20px">{{judgeSerial+1}}、</span>
          <div style="margin-top: 5px">
            &nbsp;&nbsp;&nbsp;&nbsp;{{judge[judgeSerial].belong_question.question}}
          </div>
        </div>
        <div id="judge" style="margin-top:10px;">
         <div>
         <el-radio-group v-model="judgeRadio" @change="judgeRadioChange">
          <el-radio label="0" border >正确</el-radio>
          <el-radio label="1" border style="margin-left: 10px">错误</el-radio>
         </el-radio-group>
         <div v-show="show" style="margin-top: 15px;margin-left:2px">
          <span>正确答案：</span><span style="color:#F56C6C">{{judge[judgeSerial].belong_question.right_answer}}</span>
          </div>
        </div>
         
        </div>
      </el-card>

    </div>
    <div id="control">
    <el-button type="primary" size="small" icon="el-icon-arrow-left" @click="back" style="float:left;margin-left:15px">上一题</el-button>
  <el-button type="primary" size="small" style="float:right;margin-right:15px" @click="next">下一题<i class="el-icon-arrow-right el-icon--right"></i></el-button>
  
    </div>
    
  </div>
</template>

<script>
import TimeCountDown from '@/components/TimeCountDown.vue'
import axios from "axios";
import Qs from "qs"
export default {
  data() {
    return {
      mode:0,
      radio: 4,
      apiurl:'',
      drawer:true,
      choiceShow:false,
      multipleShow:false,
      judgeShow:false,
      choiceEn:false,
      multipleEn:false,
      judgeEn:false,
      x:false,
      choice:[	{
        "belong_question": {
          "id": 14,
          "question": "低压开关：\n    空调系统压力降低至（  ）时，低压开关断开。当压力升高至至（  ）以上时，低压开关导通。",
          "answer_A": "0.196MPa、0.196MPa",
          "answer_B": "2.225MPa、2.225MPa",
          "answer_C": "2.225MPaMPa、0.196MPa",
          "answer_D": "0.196MPa、2.225MPaMPa",
          "right_answer": "A",
          "analysis": "暂无",
          "level": "1",
          "remarks": "",
          "type": 0,
          "belong_class": 1,
          "belong_type": 3
        }
      },],
      choiceSerial:0,
      multiple:[{
        "belong_question": {
          "id": 1,
          "question": "ok看看",
          "answer_A": "w",
          "answer_B": "f",
          "answer_C": "d",
          "answer_D": "c",
          "right_answer": [
            "A",
            "C",
            "D"
          ],
          "analysis": "暂无",
          "level": "1",
          "remarks": "222",
          "type": 0,
          "belong_class": null,
          "belong_type": null
        }
      },],
      multipleSerial:0,
      judge:[	{
      "belong_question": {
        "id": 1,
        "question": "你猜",
        "right_answer": "F",
        "analysis": "暂无",
        "level": "1",
        "remarks": null,
        "type": 0,
        "belong_class": null,
        "belong_type": null
      }
    },],
      judgeSerial:0,
      choiceAnswer:[],
      checkList: [],
      multipleAnswer:[],
      judgeAnswer: [],
      judgeRadio:2,
      score:0,
      total:0,
      show:false,
      showTotal:false,
      showHind:true,
      testArray:[1,2,3,4],
      tmpChoicearr:[],
      times:120,
    };
  },
  components: {
			TimeCountDown
		},
    computed:{
      getTimes(){
        return this.$store.state.times;
      }

    },
  watch:{
    'multipleSerial':{
      handler:function(NewValue,OldValue){
        // console.log(OldValue)
        if(this.multipleAnswer[NewValue] != null){
           this.multipleAnswer[OldValue]=this.storeCheckList(this.checkList.sort())
            this.checkList=this.multipleAnswer[this.multipleSerial]
           
          }else{
            // this.checkList=null
        if(this.checkList != 0){
          
          this.multipleAnswer[OldValue]=this.storeCheckList(this.checkList.sort())
          // this.multipleAnswer[OldValue]=this.checkList.sort()
        console.log(this.multipleAnswer)
          this.checkList=[]
          if(this.multipleAnswer[NewValue] != null){
          this.checkList=this.multipleAnswer[NewValue]
        }
      }else{
        // console.log(2)
         if(this.multipleAnswer[NewValue] != null){
          this.checkList=this.multipleAnswer[NewValue]
        }
      }
      }
      }
    },
    'mode':{
      handler:function(NewValue,OldValue){
        if(NewValue==0){
          this.choiceShow=true
          this.multipleShow=false
          this.judgeShow=false
        }else if(NewValue==1){
          this.choiceShow=false
          this.multipleShow=true
          this.judgeShow=false
        }else if(NewValue==2){
          this.choiceShow=false
          this.multipleShow=false
          this.judgeShow=true
        }
       if(OldValue ==1){
         if(this.checkList != 0){
        //  this.multipleAnswer[this.multipleSerial]=this.checkList
        this.multipleAnswer[this.multipleSerial]=this.storeCheckList(this.checkList.sort())
        console.log(this.multipleAnswer)
         }
       }
      }
    }

  },
  

  created(){
    // this.times = this.$store.state.times
    this.apiurl=this.$store.state.apiurl
  },
  mounted(){
    
    this.getexamnum()
    // this.testArray.sort(function(){return Math.random()>0.5?-1:1;});
//  alert(testArray);
    
    // this.order()
  },
  methods:{
    getexamnum(){
      axios({
        url:this.apiurl+"/getexinfo/?exid="+this.$store.state.examId,
        type: "json",
        method: "get",
      }).then((res) => {
        if(res.data.choice==0 && res.data.multiple==0 && res.data.judge==0){
          this.$message.error("当前考试下没有题目")
        this.$router.push({ path: "/" });
        }else{
        if (res.data.choice!=0) {
          this.getChoice()
        }
        if (res.data.multiple!=0) {
          this.getMultiple()
        }
        if (res.data.judge!=0){
          this.getJudge()
        }
        }
      })
    },
    order(){
      var be = [0,1,2,3]
      this.tmpChoicearr.length=4
      be.sort(function(){return Math.random()>0.5?-1:1;});
      this.tmpChoicearr = be
      // be = [this.choice[this.choiceSerial].belong_question.answer_A,this.choice[this.choiceSerial].belong_question.answer_B,this.choice[this.choiceSerial].belong_question.answer_C,this.choice[this.choiceSerial].belong_question.answer_D]
      for(var i=0;i<4;i++) {
        if(be[i]==0){
          be[i] = this.choice[this.choiceSerial].belong_question.answer_A
        }else if(be[i]==1){
          be[i] = this.choice[this.choiceSerial].belong_question.answer_B
        }else if(be[i]==2){
          be[i] = this.choice[this.choiceSerial].belong_question.answer_C
        }else if(be[i]==3){
          be[i] = this.choice[this.choiceSerial].belong_question.answer_D
        }
      }
      console.log(this.tmpChoicearr)
      console.log(be)
      return be;
    },
    storeCheckList(numList){
      var List=[]
      for(var i=0;i<4;i++) {
        switch(numList[i]){
          case 'A':
            List[0]='A'
            break;
          case 'B':
            List[1]='B'
            break;
          case 'C':
            List[2]='C'
            break;
          case 'D':
            List[3]='D'
            break;
          default:
            break;
        }
      }
      return List
    },
    start(){
      var questionNum = 0
      // console.log(this.multipleEn,this.choiceEn,this.judgeEn)
      // console.log(this.multiple.length)
      if(this.multiple.length==0 && this.choice.length==0 && this.judge.length==0) {
        this.$message.error("当前考试下没有题目")
        this.$router.push({ path: "/" });
      }
      if(this.choiceEn==true){
        questionNum = questionNum+this.choice.length
      }
      if(this.multipleEn==true){
        questionNum = questionNum+this.multiple.length
      }
      if(this.judgeEn==true){
        questionNum = questionNum+this.judge.length
      }
      // console.log(questionNum)
      this.score=(100/questionNum).toFixed(2)
      // console.log(Math.floor(this.score/21))
      console.log(this.score)
      if(this.choiceEn==true){
        this.choiceShow=true;
        this.multipleShow=false
        this.judgeShow=false
        this.mode=0
      }else{
        if(this.multipleEn==true){
        this.choiceShow=false;
        this.multipleShow=true;
        this.judgeShow=false;
        // console.log("Multiple")
        this.mode=1
        }else if(this.choiceEn==false){
        if(this.multipleEn==false){
        console.log(225)
        this.choiceShow=false;
        this.multipleShow=false;
        this.judgeShow=true;
        this.mode=2
        // console.log(this.judgeShow)
        }
        
      }
        
      
      }
      
    },
    getChoice(){
      axios({
        url: this.apiurl+"/getchoice_topic/?id="+this.$store.state.examId,
        type: "json",
        method: "get",
      }).then((res) => {
        // this.sites=res.data;
        // this.resources=res.data;
        console.log(res.data);
        if (res.data == "[]") {
          this.mode=1
        } else {
          this.choice = res.data;
          this.choiceEn=true;
          this.choiceShow=true;
           this.start()
        }
        }).catch(function (err) {
          console.log(err)
        })
        ;
       
    },
    getMultiple(){
       axios({
        url: this.apiurl+"/getmultiple_topic/?id="+this.$store.state.examId,
        type: "json",
        method: "get",
      }).then((res) => {
        // this.sites=res.data;
        // this.resources=res.data;
        console.log(res.data);
        if (res.data == "[]") {
          this.mode=2
        } else {
          this.multiple = res.data;
          this.multipleEn=true;
          // if(this.multipleShow==false) {
          //   this.multipleShow=true;
          // }
          this.start()
        }
        }).catch(function (err) {
         console.log(err)
          
        })
        ;
        
    },

    getJudge(){
      axios({
        url: this.apiurl+"/getjudge_topic/?id="+this.$store.state.examId,
        type: "json",
        method: "get",
      }).then((res) => {
        // this.sites=res.data;
        // this.resources=res.data;
        console.log(res.data);
        if (res.data == "[]") {
          this.mode=0
        } else {
          this.judge = res.data;
          this.judgeEn=true;
          // if(this.choiceEn==false) {
          //  if(this.multipleEn==false) {
          //    this.judgeShow=true;
          //    console.log("ju")
          //  }
           
            
          // }
          this.start()
        }
        }).catch(function (err) {
          console.log(err)
        })
        ;
        
    },


    next(){
      if(this.mode == 0){
        if(this.choiceSerial == this.choice.length-1){
          if(this.multipleEn==true){
            this.mode=1
            this.multipleSerial=0
          }else if(this.judgeEn==true){
            this.mode=2
            this.judgeSerial=0
          }else{
            this.drawer=true
          }
        }else{
          // console.log(this.choiceAnswer)
          this.choiceSerial+=1
          if(this.choiceAnswer[this.choiceSerial] != null){
          this.radio=this.choiceAnswer[this.choiceSerial]
        }else{
          this.radio=null
        }
        }
        
      }else if(this.mode==1) {
        if(this.multipleSerial == this.multiple.length-1){
          if(this.judgeEn==true){
            this.mode=2
            this.judgeSerial=0
          }else{
            this.drawer=true
          }
        }else{
          // console.log(this.choiceAnswer)
          this.multipleSerial+=1
          // if(this.multipleAnswer[this.multipleSerial] != null){
          //   this.checkList=this.multipleAnswer[this.multipleSerial]
          // }
        }  
      }else if(this.mode==2) {
        if(this.judgeSerial == this.judge.length-1){
          this.drawer=true
        }else{
          // console.log(this.choiceAnswer)
          this.judgeSerial+=1
          if(this,this.judgeAnswer[this.judgeSerial] != null){
            this.judgeRadio = this.judgeAnswer[this.judgeSerial]
          }else{
             this.judgeRadio=null
          }
         
          // console.log(this.judgeSerial)
        }  
      }
    },
    back() {
      if(this.mode == 0){
        if(this.choiceSerial==0) {
          this.$message.error('这是第一道题');
        }else{
           this.choiceSerial=this.choiceSerial-1
           if(this.choiceAnswer[this.choiceSerial] != null){
          this.radio=this.choiceAnswer[this.choiceSerial]
        }else{
          this.radio=null
        }
        }
       
      }else if(this.mode==1) {
        if(this.multipleSerial == 0){
          if(this.choiceEn==true){
            this.mode=0
            this.choiceSerial=this.choice.length-1
          }else{
            this.drawer=true
          }
        }else{
          // console.log(this.choiceAnswer)
          this.multipleSerial=this.multipleSerial-1
          // if(this.multipleAnswer[this.multipleSerial] != null){
          //   this.checkList=this.multipleAnswer[this.multipleSerial]
          // }else{
          //   this.checkList=null
          // }
        }  
      }
      else if(this.mode==2) {
        if(this.judgeSerial == 0){
          if(this.multipleEn==true){
            this.mode=1
            this.multipleSerial=this.multiple.length-1
          }
        }else{
          // console.log(this.choiceAnswer)
          this.judgeSerial=this.judgeSerial-1
           if(this.judgeAnswer[this.judgeSerial] != null){
            this.judgeRadio = this.judgeAnswer[this.judgeSerial]
          }
        }  
      }
    },
    radioChange(){
      console.log(this.radio)

      this.choiceAnswer[this.choiceSerial]=this.radio
    },
    checkboxChange(){
      console.log(this.checkList)
      // this.multipleAnswer[this.multipleSerial]=this.checkList
      // this.checkList=[]
    },
    saveMultipleAnswer(){
      this.multipleAnswer[this.multipleSerial]=this.checkList
      console.log(this.multipleAnswer)
    },
    judgeRadioChange(){
      if(this.judgeRadio != null){
      this.judgeAnswer[this.judgeSerial]=this.judgeRadio
      }
    },
    toList(){
      this.$router.push({ path: "/" });
    },
    headIn(){
      // var grades = 0
      this.total=0
      if(this.choiceEn==true){
        this.total=this.total+this.goOverChoice()
      }
      if(this.multipleEn==true){
        this.total=this.total+this.goOverMultiple()
      }
      if(this.judgeEn==true){
        this.total=this.total+this.goOverJudge()
      }
      // console.log(this.total)
      if(parseFloat(this.total)>100){
      this.total=100
      // console.log(this.total)
      }
      var exid = this.$store.state.examId
      // console.log(exid)
      var sid = this.$store.state.sid
      var score = this.total
      axios({
        url: this.apiurl+"/subgrades/",
        type: "json",
        method: "post",
        data:Qs.stringify.params({
          exid,
          sid, 
          score
        }),
        headers:{
                  "Content-Type":"application/x-www-form-urlencoded"
              },
      }).then((res) => {
        // console.log(res.data);
        if (res.data.length == 0) {
          this.$message.error("提交失败");
        }else if(res.data=='same'){
          this.$message.error("本考试已存在成绩")
        }else if(res.data=='error'){
          this.$message.error("提交错误")
        }else if(res.data=='date'){
          this.$message.error("考试已结束，无法提交成绩")
        }
         else {
          this.$message.success("提交成功")
          this.showHind=false
          if(this.$store.state.show==true){
            this.show=true;
            this.showTotal=true;
          }else{
            this.show=false;
            this.showTotal=true;
          }
          // console.log(Date.parse(this.exam[0].start_date)>new Date())
        }
      })
      
    },
    showtotal(){
      this.$message.success(this.total)
    },
    closeTotal(){
      console("555")
      if(this.$store.state.show==false){
         this.toList()
         console.log("444")
      }
    },
    getChoiceAw(num){
      switch(num){
        case 0:
          return 'A'
        case 1:
          return 'B'
        case 2:
          return 'C'
        case 3:
          return 'D'
        default:
        break;
      }
      
    },
    getJudgeAw(num){
      switch(num){
        case "0":
          return 'T'
        case "1":
          return 'F'
        default:
          break;
      }
    },
    goOverChoice(){
      var choicesore = 0
      for(var i=0; i<this.choice.length;i++){
        // console.log(i)
        // console.log(this.choice[i].belong_question.right_answer)
        if(this.choice[i].belong_question.right_answer==this.getChoiceAw(this.choiceAnswer[i])){
          choicesore=parseFloat(choicesore) + parseFloat(this.score)
          
        }
      }
      // console.log(choicesore)
      return choicesore
      
    },
    goOverJudge(){
      var judgesore =0
      for(var i=0;i<this.judge.length;i++){
        if(this.judge[i].belong_question.right_answer==this.getJudgeAw(this.judgeAnswer[i])){
          judgesore=parseFloat(judgesore) + parseFloat(this.score)
        }

      }
      // console.log(judgesore)
      return judgesore
    },
    goOverMultiple(){
      var multiplesore = 0.00
      // // var sor=0
      // console.log(this.multipleAnswer)
      // console.log(this.storeCheckList(this.multiple[0].belong_question.right_answer))
      for(var i=0;i<this.multiple.length;i++){
        var sore = 0
        var ra = this.storeCheckList(this.multiple[i].belong_question.right_answer)
        // var ra = this.multiple[i].belong_question.right_answer
        var ma = this.multipleAnswer[i]
        // console.log(ra,ma)
        var d = 0
        if(ma != null){
        for(var a=0;a<4;a++){
          
          if(ra[a]==ma[a]){
            d=d+1
            continue
          }else if(ra[a]!=ma[a] && ma[a]==null){
            sore = (this.score/2).toFixed(2)
            continue
          }else{
            sore=0
            break;
          }
        }
      }
        if(d==4){
          sore=this.score
          
        }
        // console.log("成绩"+sore)
        multiplesore = parseFloat(multiplesore)+parseFloat(sore)
        // console.log(multiplesore)
      }

      //  console.log("评分结束")
      //  console.log(multiplesore)
       return multiplesore
    }
   

  }


};
</script>

