<template>
    <div>
    <div style="margin-top: 10px">
    <el-button type="text" icon="el-icon-arrow-left" style="margin-left: 10px" @click="toList()"
        >返回考试列表</el-button
      >
    </div>
    <div style="margin-left:5px;margin-right:5px">
    <el-table
    :data="tableData"
    stripe
    style="width: 100%">
    <el-table-column
      prop="index"
      label="排名"
      width="50%">
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      width="70%">
    </el-table-column>
    <el-table-column
      prop="score"
      label="成绩"
      width="70%">
    </el-table-column>
    <el-table-column
      prop="date"
      label="提交时间">
    </el-table-column>
  </el-table>
    </div>
    </div>
</template>

<script>
import axios from "axios";
export default {
    data(){
        return{
            apiurl:'',
            tableData:[],
        };
    },
    mounted(){
        this.apiurl=this.$store.state.apiurl;
        this.getData()
    },
    methods:{
        getData(){
            axios({
                url: this.apiurl+"/getGrades/?exid="+this.$store.state.examId,
                type: "json",
                method: "get",
        }).then((res)=>{
            // console.log(res.data);
            this.tableData=res.data;
        })
    },
    toList(){
      this.$router.push({ path: "/" });
    },
    }

}
</script>