<template>
  <div>
  <div style="height:15px;"></div>
    <div>
      <el-row :gutter="20" style="margin-right:+5px;margin-left:5px;" v-loading="loading">
        <div v-for="item in exam" :key="item.id">
          <el-col  :lg="6" class="djweb">
            <div style="height: 250px">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span>{{ item.name }}</span>
                  
                  <el-button style="float: right; padding: 3px 0" type="text" @click="toExam(item.id,item.start_date,item.end_date,item.show,item.remarks,item.duration)"
                    >开始考试</el-button
                  >
                  <el-button style="float: right; padding: 3px 0;margin-right: 5px" type="text" @click="toRank(item.id)"
                    >查看排名</el-button
                  >
                  
                </div>
                <div class="text item">考试班级：{{ item.group.cl_name }}</div>
                <div class="text item">
                  开始时间：{{ item.start_date.substring(0, 10) }}
                  {{ item.start_date.substring(11, 16) }}
                </div>
                <div class="text item" style="color: #F56C6C">
                  结束时间：{{ item.end_date.substring(0, 10) }}
                  {{ item.end_date.substring(11, 16) }}
                </div>
                <div class="text item">教师：{{ item.belong_te.name }}</div>
              </el-card>
            </div>
          </el-col>
        </div>
      </el-row>
     
      
    </div>
  </div>
</template>



<script>
import axios from "axios";
export default {
  data() {
    return {
      id: "1",
      apiurl:'',
      exam: [],
      loading: true,
      
    };
  },

  mounted: function () {
    
    this.apiurl=this.$store.state.apiurl;
    this.getList();
    window.scanCallBack = this.scanCallBack;
    Window.alertSid = this.alertSid;
  },

  methods: {
    scanCallBack: function (str) {
      this.id = str;
      this.getList();

    },
    alertSid: function (str) {
      this.$store.state.sid=str;
    },

    getList() {
      console.log(this.apiurl+"/examin/?class="+this.id)
      axios({
        url: this.apiurl+"/examin/?class="+this.id,
        type: "json",
        method: "get",
      }).then((res) => {
        // this.sites=res.data;
        // this.resources=res.data;
        console.log(res.data);
        if (res.data.length == 0) {
          this.$message.error("当前没有考试");
        } else {
          this.exam = res.data;
          // console.log(Date.parse(this.exam[0].start_date)>new Date())
          this.loading=false;
       }
        if(this.exam.length==0){
        this.$message.error("获取考试错误，当前班级没有考试");
      }
      }).catch(function(error){
        console.error(error);
      })
      ;
      
    },
    toExam(examId,start_date,end_date,show,remarks,times){
      if(Date.parse(start_date)>new Date()){
        this.$message("考试尚未开始")
      }else if(Date.parse(end_date)<new Date()){
        this.$message("考试已结束")
      }else{
        if(remarks != null){
         this.$notify({
          title: '考试备注',
          message: remarks,
          duration: 0
        });
        }
      console.log(remarks)
      console.log(times)
      this.$store.state.times=times
      this.$store.state.show=show
      console.log(show)
      console.log(this.$store.state.show)
      this.$store.state.examId=examId
      console.log(examId);
      this.$router.push({ path: "/exam" });
      }
    },
    toRank(examId){
      this.$store.state.examId=examId
      this.$router.push({ path: "/rank" });
    }
  },
};
</script>
<style>
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: auto;
  height: auto;
}
</style>